.layer.is {
  opacity: 1;
}

.back-layer.is {
  opacity: 1;
}

.layer.not {
  opacity: 0;
  -webkit-animation: fadeOut 0.3s ease-out;
          animation: fadeOut 0.3s ease-out;
}

.back-layer.not {
  opacity: 0;
