.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-2-3 {
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-2-1 {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-3-1 {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.grid-1-3 {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-1-4 {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-4-1 {
  display: grid;
  grid-template-columns: 4fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-1-10 {
  display: grid;
  grid-template-columns: 1fr 15fr;
  grid-gap: 2rem;
  gap: 2rem;
  align-content: top;
}

.grid-2-row {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.gap-half {
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.gap-none {
  grid-gap: 0;
  gap: 0;
}

.gap-none {
  grid-gap: 0;
  gap: 0;
}

.btn {
  display: inline-block;
