/* Forms */
input {
  margin: 1.2rem 0;
}

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.normal-textarea {
  font-family: 'Roboto', sans-serif;
  font-size: .9rem;
  resize: none;
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='date'],
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.3rem;
  font-size: 1rem;
  border: 1px solid #ccc;
}

input[type='text']:focus,
input[type='email']:focus,
input[type='password']:focus,
input[type='date']:focus,
select,
textarea:focus {
  outline: none;
}

input[type='submit'],
