@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes layersComeAnim1 {
  0% {
    -webkit-transform: skew(0deg);
            transform: skew(0deg);
  }
  100% {
    -webkit-transform: skew(-45deg);
            transform: skew(-45deg);
  }
}

@keyframes layersComeAnim1 {
  0% {
    -webkit-transform: skew(0deg);
            transform: skew(0deg);
  }
  100% {
    -webkit-transform: skew(-45deg);
            transform: skew(-45deg);
  }
}

@-webkit-keyframes layersComeAnim2 {
  0% {
    -webkit-transform: skew(0);
            transform: skew(0);
    border-radius: 70px;
  }
  100% {
    -webkit-transform: skew(45deg);
            transform: skew(45deg);
    border-radius: 0;
  }
}

@keyframes layersComeAnim2 {
  0% {
    -webkit-transform: skew(0);
            transform: skew(0);
    border-radius: 70px;
  }
  100% {
    -webkit-transform: skew(45deg);
            transform: skew(45deg);
    border-radius: 0;
  }
}

@-webkit-keyframes layersGoAnim1 {
  0% {
    border-radius: 70px;
    -webkit-transform: skew(-45deg);
            transform: skew(-45deg);
  }
  100% {
    -webkit-transform: skew(0deg);
            transform: skew(0deg);
    border-radius: 0;
  }
}

@keyframes layersGoAnim1 {
